.subscribe {
	position: relative;
	z-index: 1;
	padding: 60px 0;
	background-color: rgba(0, 0, 0, 0.8);
	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;

		width: 100%;
		height: 100%;
		background: url(../img/subscribe-bg.jpeg) center/cover no-repeat;
		filter: grayscale(100%) brightness(15%);
		opacity: 0.7;
	}

	&__container {
		position: relative;
		z-index: 3;
	}

	&__info {
		max-width: 460px;
		margin: 0 auto;
		margin-bottom: 60px;

		text-align: center;
	}

	&__title {
		color: #fff;
		margin-bottom: 40px;
	}

	&__text {
		color: #81868e;
	}

	&__form {
		display: flex;
		max-width: 520px;
		margin: 0 auto;
	}

	&__input {
		flex: 1 0 auto;
		height: 40px;
		padding-left: 25px;
		margin-right: 10px;

		background-color: transparent;
		border: 1px solid #fff;
		color: #fff;

		transition: all 0.2s ease-in;
		&:hover {
			border: 1px solid $accent-color;
			color: $accent-color;
		}
		&:focus {
			outline: none;
			border: 1px solid $accent-color;
			color: $accent-color;
			&::placeholder {
				color: #fff;
				transition: color 0.2s ease-in;
			}
		}
	}
	&__submit {
		flex: 0 1 auto;
		padding: 0 50px;
		background: $accent-color;

		border: 1px solid $accent-color;
		color: #ffffff;

		line-height: 38px;

		transition: all 0.2s ease-in;
		&:hover {
			background: transparent;
			color: $accent-color;
		}
		&:focus {
			outline: none;
			background: transparent;
			color: $accent-color;
		}
	}
	@media (max-width: $md4+px) {
		&__info {
			margin-bottom: 40px;
		}
		&__title {
			margin-bottom: 20px;
		}
		&__form {
			flex-wrap: wrap;
			justify-content: center;
		}
		&__input {
			flex: 0 0 100%;
			margin-bottom: 10px;
			margin-right: 0;
		}
	}
}
