.footer {
	padding: 60px 0;
	background-color: #f3f3f3;
	&__container {
		text-align: center;
	}

	&__logo {
		color: #323a45;
		margin-bottom: 24px;
	}

	&__copy {
		margin-bottom: 30px;
		color: #81868e;
		font-family: $ffOS;
		font-size: 14px;
	}

	&__links {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__link {
		position: relative;

		width: 27px;
		height: 27px;
		border-radius: 50%;

		transition: transform 0.2s ease;
		&:not(:last-child) {
			margin-right: 10px;
		}
		&:hover {
			transform: scale(1.1);
		}
		&::after {
			content: "";
			position: absolute;
			top: 0;
			left: 0;

			width: 100%;
			height: 100%;
			border-radius: 50%;
		}
	}

	&__link--fb {
		background-color: #3b5998;
		&::after {
			background: url(../img/footer/fb.svg) center center no-repeat;
		}
	}

	&__link--tw {
		background-color: #26a6d1;
		&::after {
			background: url(../img/footer/tw.svg) center center no-repeat;
		}
	}

	&__link--ld {
		background-color: #0e76a8;
		&::after {
			background: url(../img/footer/in.svg) center center no-repeat;
		}
	}

	&__link--in {
		background-color: #ee6fee;
		&::after {
			background: url(../img/footer/inst.svg) center center no-repeat;
		}
	}

	&__link--vm {
		background-color: #86c9ef;
		&::after {
			background: url(../img/footer/vm.svg) center center no-repeat;
		}
	}
}
