.testimonials {
	background-color: #f3f3f3;
	padding: 60px 0;
	&__container {
	}

	&__promo {
		max-width: 460px;
		margin: 0 auto;
		margin-bottom: 50px;

		text-align: center;
	}

	&__title {
		margin-bottom: 30px;
	}

	@media (max-width: $md3+px) {
		&__promo {
			margin-bottom: 30px;
		}
		&__title {
			margin-bottom: 20px;
		}
	}
}
.slider-tst__container .swiper-pagination-bullets {
	bottom: 30px;
}
.slider-tst__container .swiper-pagination-bullet {
	width: 10px;
	height: 10px;
	background-color: transparent;
	border: 1px solid $accent-color;
}
.slider-tst__container .swiper-pagination-bullet-active {
	background-color: $accent-color;
	border: 1px solid $accent-color;
}
.slider-tst {
	&__container {
		background-color: #fff;
		padding: 60px 0;
	}
	&__wrapper {
		display: flex;
		max-width: 1000px;
		margin: 0 auto;
	}

	&__image {
		padding: 0 20px;
	}

	&__img {
		border-radius: 50%;
		border: 2px solid $accent-color;

		width: 130px;
		height: 130px;
		object-fit: cover;
	}

	&__info {
		font-size: 16px;
		text-align: justify;
		padding-right: 20px;
	}

	&__text {
		margin-bottom: 30px;
	}

	&__name {
		text-transform: uppercase;
		margin-bottom: 10px;
	}

	&__profession {
		font-size: 14px;
		text-transform: uppercase;
	}
	@media (max-width: $md3+px) {
		&__container {
			padding-top: 40px;
		}
	}
	@media (max-width: $md4+px) {
		&__wrapper {
			flex-direction: column;
		}
		&__image {
			align-self: center;
			margin-bottom: 20px;
		}
		&__info {
			padding-left: 20px;
		}
	}
}
