.news {
	position: relative;
	z-index: 1;
	padding: 60px 0;
	background-color: rgba(0, 0, 0, 1);
	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;

		width: 100%;
		height: 100%;
		background: url(../img/news-bg.jpg) center/cover no-repeat;
		filter: grayscale(100%) brightness(15%);
		opacity: 0.7;
	}
	&__container {
		position: relative;
		z-index: 3;
	}

	&__info {
		max-width: 460px;
		margin-bottom: 70px;
		color: #fff;
	}

	&__title {
		margin-bottom: 40px;
		color: #fff;
	}

	&__text {
		color: #fff;
	}
	@media (max-width: $md3+px) {
		&__title {
			margin-bottom: 20px;
		}
	}
	&__info {
		margin-bottom: 40px;
	}
}

.news-slider .swiper-pagination-bullet {
	width: 10px;
	height: 10px;
	background-color: transparent;
	border: 1px solid #b2b3b3;
}
.news-slider .swiper-pagination-bullet-active {
	background-color: #b2b3b3;
	border: 1px solid #b2b3b3;
}
.news-slider {
	overflow: hidden;
	height: 300px;
	&__slide {
		padding-left: 70px;
		padding-right: 30px;
	}
	&__wrapper {
		padding-bottom: 10px;
		max-width: 600px;
	}
	&__title {
		margin-bottom: 10px;
		color: #e1e4e9;

		font-family: $ffOS;
		font-size: 20px;
	}

	&__author {
		color: #e1e4e9;
		margin-bottom: 20px;

		font-weight: 700;
		text-transform: uppercase;
	}

	&__info {
		margin-bottom: 60px;
		color: #c9c9c9;

		font-family: $ffSS;
		font-weight: 300;
		font-size: 14px;
	}

	&__text {
		@extend %scroll-del;
		overflow-y: auto;
		overflow-x: hidden;

		max-height: 100px;
		margin-bottom: 50px;

		text-align: justify;
	}

	&__button {
		background: transparent;
		color: #b2b3b3;
		font-family: $ffOS;
		font-size: 16px;
	}
	@media (max-width: $md3+px) {
		height: 240px;
		&__slide {
			padding-left: 0;
		}
		&__wrapper {
			max-width: none;
		}
		&__info {
			margin-bottom: 30px;
		}
		&__text {
			margin-bottom: 20px;
		}
	}
}
