@charset "UTF-8";

@import url("https://fonts.googleapis.com/css?family=Open+Sans:regular,700");

@import url("https://fonts.googleapis.com/css?family=Roboto:300");

@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,300italic,regular,900");

* {
  margin: 0;
  padding: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

ol {
  list-style-position: inside;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
  margin: 0;
}

@font-face {
  font-family: "Big John";
  font-display: swap;
  src: url("../fonts/BIG JOHN.woff") format("woff"), url("../fonts/BIG JOHN.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

.news-slider__text {
  -ms-overflow-style: none;
}

.news-slider__text::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

html {
  min-height: 100%;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

body {
  position: relative;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

.wrapper {
  height: 100%;
}

.lock {
  overflow: hidden;
}

.container {
  max-width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
}

.title {
  color: #333b46;
  text-transform: uppercase;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 20px;
}

.text {
  line-height: 150%;
  color: #81868e;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
}

.link {
  display: inline-block;
  color: #333b46;
  padding: 12px 30px;
  border: 1px solid #333b46;
  border-radius: 30px;
  text-align: center;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  transition: all 0.2s ease;
}

.link:hover {
  color: #ffffff;
  background-color: #000000;
}

.color--green {
  color: #19bd9a;
  font-weight: 700;
}

.subtitle {
  color: #464646;
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
}

.icon-body {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #81868e;
  width: 54px;
  height: 54px;
  transition: all 0.2s ease;
}

.icon-body:hover {
  background-color: #19bd9a;
}

.icon-img {
  width: 30px;
  height: 30px;
}

.submit-btn {
  background: #19bd9a;
  border: 1px solid #19bd9a;
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  text-transform: uppercase;
  transition: all 0.2s ease-in;
}

.submit-btn:hover {
  background: transparent;
  color: #19bd9a;
}

.submit-btn:focus {
  outline: none;
  background: transparent;
  color: #19bd9a;
}

.header {
  position: relative;
  height: 100%;
  width: 100%;
  min-height: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: url(../img/header/header-bg.jpg) center/cover no-repeat;
}

.header::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.404);
  z-index: 1;
}

.header__info {
  position: relative;
  z-index: 2;
  text-align: center;
}

.header__title {
  color: #fff;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 300;
  font-size: 35px;
  letter-spacing: 5px;
  text-transform: uppercase;
  margin-bottom: 40px;
}

.header__promo {
  max-width: 600px;
  color: #fff;
  padding-bottom: 60px;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 1.2;
}

.header__scroll-link {
  position: absolute;
  z-index: 3;
  bottom: 70px;
  left: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 37px;
  height: 37px;
  border-radius: 50%;
  background-color: #0a856a;
  transform: translateX(-50%);
  -webkit-animation: hoverWave linear 2.5s infinite;
  animation: hoverWave linear 2.5s infinite;
  transition: all 0.3s ease;
}

.header__scroll-link:hover {
  animation: hoverWaveBigger linear 2s infinite;
}

@-webkit-keyframes hoverWave {
  0% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 0 rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  40% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 15px rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  80% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 26.7px rgba(25, 189, 154, 0.067);
  }

  100% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 40px rgba(25, 189, 154, 0);
  }
}

@keyframes hoverWave {
  0% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 0 rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  40% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 15px rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  80% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 26.7px rgba(25, 189, 154, 0.067);
  }

  100% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 40px rgba(25, 189, 154, 0);
  }
}

@-webkit-keyframes hoverWaveBigger {
  0% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 0 rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  40% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 50px rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  80% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 60px rgba(25, 189, 154, 0), 0 0 0 66.7px rgba(25, 189, 154, 0.067);
  }

  100% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 65px rgba(25, 189, 154, 0), 0 0 0 80px rgba(25, 189, 154, 0);
  }
}

@keyframes hoverWaveBigger {
  0% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 0 rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  40% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 50px rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  80% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 60px rgba(25, 189, 154, 0), 0 0 0 66.7px rgba(25, 189, 154, 0.067);
  }

  100% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 65px rgba(25, 189, 154, 0), 0 0 0 80px rgba(25, 189, 154, 0);
  }
}

.nav._active::before {
  background-color: #7c703a;
}

.nav._active .nav__body {
  height: 60px;
}

.nav {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
  transition: all 0.2s ease;
}

.nav::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  z-index: 2;
  transition: all 0.2s ease;
}

.nav__body {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 114px;
  align-items: center;
  transition: all 0.2s ease;
}

.nav__logo {
  position: relative;
  z-index: 3;
  padding-bottom: 10px;
  color: #fff;
  font-family: "Big John";
  font-weight: 700;
  font-size: 25px;
  text-transform: uppercase;
}

.nav__burger {
  display: none;
}

.nav__list {
  display: flex;
}

.nav__list li {
  position: relative;
  z-index: 2;
}

.nav__list li:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  display: inline-block;
  color: #fff;
  padding: 5px 15px;
  border: 1px solid transparent;
  border-radius: 15px;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  transition: all 0.2s ease;
}

.nav__link:hover {
  border: 1px solid #fff;
}

.nav.nav--active::before {
  background: #4b4f14;
  background: -moz-linear-gradient(90deg, #4b4f14 0%, #885a05 64%);
  background: -webkit-linear-gradient(90deg, #4b4f14 0%, #885a05 64%);
  background: linear-gradient(90deg, #4b4f14 0%, #885a05 64%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#4b4f14",endColorstr="#885a05",GradientType=1);
}

.nav.nav--active .nav__body {
  height: 50px;
}

.promo__container {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  max-width: 850px;
  padding-top: 80px;
  padding-bottom: 50px;
}

.promo__info {
  text-align: justify;
  margin-right: 50px;
}

.promo__title {
  padding-bottom: 50px;
}

.promo__text {
  padding-bottom: 30px;
  line-height: 200%;
}

.promo__link {
  background: transparent;
}

.promo__image {
