.details {
  background-color: #f3f3f3;
  padding-top: 60px;
  padding-bottom: 80px;
}

.details__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.details__promo {
  text-align: center;
}

.details__title {
  margin-bottom: 40px;
