.projects {
  padding-top: 60px;
}

.projects__promo {
  max-width: 460px;
  margin: 0 auto;
  margin-bottom: 60px;
  padding: 0 10px;
  text-align: center;
}

.projects__title {
  margin-bottom: 40px;
}

.projects__works {
  display: flex;
  width: 100%;
  align-items: stretch;
}

.projects__work:nth-child(3n + 1) {
  flex: 0 0 50%;
}

.projects__work:nth-child(3n + 2) {
  flex: 0 0 25%;
}

.projects__work:nth-child(3n + 3) {
  flex: 0 0 25%;
}

.projects__work {
  position: relative;
  z-index: 1;
  outline: none;
  display: inline-block;
}

.projects__work::after {
  content: "";
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  transition: background-color 0.2s ease-in;
}

.projects__work:hover::after {
  background-color: rgba(0, 0, 0, 0);
}

.projects__work:hover .projects__work-title {
  opacity: 0;
  visibility: hidden;
}

.projects__work:hover .projects__work-img {
  filter: none;
}

.projects__work:focus::after {
  background-color: rgba(0, 0, 0, 0);
}

.projects__work:focus .projects__work-title {
  opacity: 0;
  visibility: hidden;
}

.projects__work:focus .projects__work-img {
  filter: none;
}

.projects__work-img {
  width: 100%;
  height: 320px;
  object-fit: cover;
  filter: grayscale(100%);
  transition: filter 0.2s ease-in;
}

.projects__work-title {
  position: absolute;
  top: 50%;
  left: 50%;
  visibility: visible;
  z-index: 3;
  opacity: 1;
  color: #ffffff;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 20px;
  text-transform: uppercase;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in;
}

.footer-project {
