}

.services__promo {
  max-width: 460px;
  margin: 0 auto;
  margin-bottom: 60px;
  text-align: center;
}

.services__title {
  margin-bottom: 40px;
}

.services__row {
  display: flex;
}

.services__column {
  flex: 0 0 50%;
}

.graphic {
  display: flex;
}

.graphic__icons {
  margin-right: 50px;
}

.graphic__icon:not(:last-child) {
  margin-bottom: 16px;
}

.graphic__title {
  margin-bottom: 30px;
}

.graphic__text {
  text-align: justify;
}

.graphic__text p:not(:last-child) {
  margin-bottom: 20px;
}

.progress {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.progress__wrapper {
  display: none;
}

.progress__body {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.progress__body:not(:last-child) {
  margin-right: 40px;
}

.progress__title {
  color: #81868e;
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

.progress__circle-wrap {
  width: 120px;
  height: 120px;
  background: #047378;
  border-radius: 50%;
  margin-bottom: 20px;
}

.progress__mask,
.progress .fill {
  width: 120px;
  height: 120px;
  position: absolute;
  border-radius: 50%;
}

.progress__mask {
  clip: rect(0px, 120px, 120px, 60px);
}

.progress .fill {
  clip: rect(0px, 60px, 120px, 0px);
  background-color: #19bd9a;
}

.progress .full60,
.progress .fill60 {
  animation: fill60 ease-in-out 2s;
  transform: rotate(108deg);
}

.progress .full75,
.progress .fill75 {
  animation: fill75 ease-in-out 2s;
  transform: rotate(135deg);
}

.progress .full80,
.progress .fill80 {
  animation: fill80 ease-in-out 2s;
  transform: rotate(144deg);
}

.progress__inside-circle {
  width: 110px;
  height: 110px;
  border-radius: 50%;
  background: #fff;
  line-height: 110px;
  text-align: center;
  margin-top: 5px;
  margin-left: 5px;
  position: absolute;
  z-index: 2;
  font-weight: 700;
  font-size: 30px;
}

@keyframes fill80 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(144deg);
  }
}

@keyframes fill75 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(135deg);
  }
}

@keyframes fill60 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(108deg);
  }
}

