.filter__buttons {
  margin-bottom: 40px;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  text-align: center;
}

.filter__button {
  position: relative;
  outline: none;
  display: inline-block;
  padding: 12px 10px;
  background-color: transparent;
  color: #464646;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.filter__button:not(:last-child) {
  margin-right: 20px;
}

.filter__button:hover {
  transform: scale(1.2);
}

.filter__button:focus {
  transform: scale(1.2);
}

.filter__button:focus::after {
  opacity: 1;
  transform: translateX(-50%);
}

.filter__button::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  opacity: 0;
  width: 60%;
  height: 2px;
  background-color: #19bd9a;
  transform: translateX(-50%);
  transition: all 0.3s ease;
}

.filter__button.active {
  transform: scale(1.2);
}

.filter__button.active::after {
  opacity: 1;
}

.filter__container {
  overflow: hidden;
  display: grid;
  grid-template: repeat(auto-fill, minmax(100px, 1fr))/repeat(auto-fill, minmax(240px, 1fr));
  grid-gap: 10px;
  width: 100%;
}

.filter__block {
  display: none;
  background-color: #5e676e;
  color: #ffffff;
}

.filter__block.show {
  display: block;
}

.f-block:hover .f-block__info {
  opacity: 1;
  visibility: visible;
}

.f-block__wrapper.active .f-block__info {
  opacity: 1;
  visibility: visible;
}

.f-block__wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

.f-block__image-link {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.f-block__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.f-block__info {
  position: relative;
  display: flex;
