.contact {
	&__info {
		background-color: #f3f3f3;
		padding: 60px 0;
	}
	&__form {
		padding: 60px 0;
	}
	&__container {
		max-width: 950px;
		margin: 0 auto;
	}

	&__promo {
		margin: 0 auto;
		max-width: 460px;
		margin-bottom: 60px;
		text-align: center;
	}

	&__title {
		margin-bottom: 40px;
		color: #333b46;
	}

	&__text {
		color: #81868e;
	}

	&__contacts {
		display: flex;
		margin: 0 -20px;
		justify-content: center;
	}

	&__item {
		flex: 0 0 33.33%;
		padding: 0 20px;
	}

	@media (max-width: $md2+px) {
		&__contacts {
			margin: 0;
			justify-content: space-between;
		}
	}
	@media (max-width: $md3+px) {
		&__contacts {
			flex-wrap: wrap;
			justify-content: center;
		}
		&__item {
			flex: 0 0 50%;
			&:not(:last-child) {
				margin-bottom: 40px;
			}
		}
		@media (max-width: $md4+px) {
			&__item {
				flex: 0 0 100%;
			}
		}
	}
}

.c-item {
	display: flex;
	flex-direction: column;
	&__wrapper {
		flex: 1 0 auto;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding-bottom: 30px;
		background-color: #fff;
	}

	&__image {
		flex: 0 0 auto;
		position: relative;
		height: 150px;
		width: 100%;
		margin-bottom: 30px;

		&::after {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;

			width: 70px;
			height: 50px;

			transform: translate(-50%, -50%) scale(0.8);
		}
	}
	&__image--email {
		&::after {
			background: url(../img/contact/mail.svg) center center no-repeat;
		}
	}
	&__image--address {
		&::after {
			background: url(../img/contact/home.svg) center center no-repeat;
			width: 50px;
			height: 70px;
		}
	}
	&__image--phone {
		&::after {
			background: url(../img/contact/phone.svg) center center no-repeat;
			width: 70px;
			height: 70px;
		}
	}
	&__img {
		filter: grayscale(100%) brightness(50%);
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__info {
		display: flex;
		flex-direction: column;
		flex: 1 0 auto;
		text-align: center;
	}
	&__title {
		flex: 1 0 auto;
		margin-bottom: 30px;
		color: #464646;

		font-family: $ffOS;
		font-size: 16px;
		text-transform: uppercase;
	}
	&__address {
		flex: 0 0 auto;
		color: #81868e;

		font-family: $ffOS;
		font-size: 14px;
		text-transform: uppercase;
		font-style: inherit;
	}
	&__link {
		flex: 0 0 auto;
		display: block;
		color: #81868e;

		font-family: $ffOS;
		font-size: 14px;
		text-transform: uppercase;

		transition: color 0.2s ease;
		&:not(:last-child) {
			margin-bottom: 5px;
		}
		&:hover {
			color: $accent-color;
		}
	}
}

.c-form {
	&__form {
		display: flex;
		flex-direction: column;
		max-width: 940px;
		margin: 0 auto;
	}
	&__inputs {
		display: flex;
		margin-bottom: 30px;
	}

	&__input {
		height: 40px;
		width: 100%;

		padding-left: 25px;

		background-color: transparent;
		border: 1px solid #cccccc;
		color: #868686;

		transition: all 0.2s ease-in;
		&:not(:last-child) {
			margin-right: 40px;
		}
		&:hover {
			border: 1px solid $accent-color;
			color: $accent-color;
		}

		&:focus {
			outline: none;
			border: 1px solid $accent-color;
			color: $accent-color;
		}
	}

	&__textarea {
		height: 190px;
		margin-bottom: 30px;
		padding: 20px;
		border: 1px solid #cccccc;
		color: #868686;
		resize: none;

		transition: all 0.2s ease-in;
		&:hover {
			border: 1px solid $accent-color;
			color: $accent-color;
		}
		&:focus {
			outline: none;
			border: 1px solid $accent-color;
			color: $accent-color;
		}
	}

	&__submit {
		padding: 20px 0;
	}
	@media (max-width: $md3+px) {
		&__inputs {
			flex-wrap: wrap;
		}

		&__input {
			flex: 0 0 100%;
			margin-right: 0;
			&:not(:last-child) {
				margin-bottom: 20px;
			}
		}
	}
}
.map {
	position: relative;
	z-index: 1;
	height: 450px;

	&__field {
		content: "";
		position: absolute;
		top: 0;
		left: 0;

		z-index: 2;

		width: 100%;
		height: 100%;

		background: url(../img/contact/map.jpg) center center no-repeat;
		cursor: pointer;
		filter: grayscale(100%) brightness(15%);
	}
	&:hover .map__btn {
		color: $accent-color;
	}
	&__btn {
		position: absolute;
		top: 50%;
		left: 50%;
		z-index: 3;

		padding-right: 30px;
		background: transparent;
		color: #ffffff;

		line-height: 15px;
		font-family: $ffOS;
		font-size: 16px;
		text-transform: uppercase;

		transition: all 0.2s ease-in;
		transform: translate(-50%, -50%);
		&:hover {
			color: $accent-color;
		}

		&:focus {
			outline: none;
			color: $accent-color;
		}

		&::after {
			content: "";
			position: absolute;
			right: 0;
			bottom: -2px;

			width: 20px;
			height: 20px;
			padding: 5px;

			animation: hoverWave linear 2s infinite;

			background: url(../img/contact/arrow.svg) center/10px 10px no-repeat;
			background-color: $accent-color;
			border-radius: 50%;
		}
	}
	&__btn--hidden {
		opacity: 0;
		visibility: hidden;
		transition: all 0.2s ease-in;
	}

	&__body--active {
		display: block;
	}
	&__animation {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 4;
		opacity: 0;
		visibility: hidden;

		width: 100%;
		height: 100%;

		transition: all 0.3s ease-in;
	}
	&__animation.active {
		opacity: 1;
		visibility: visible;
	}
}

iframe {
	position: relative;
	z-index: 3;

	width: 100%;
	display: none;
}
// Load map animetion
.cssload-dots {
	width: 0;
	height: 0;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	margin: auto;
	outline: 1px solid red;
	filter: url(#goo);
	-o-filter: url(#goo);
	-ms-filter: url(#goo);
	-webkit-filter: url(#goo);
	-moz-filter: url(#goo);
}

.cssload-dot {
	width: 0;
	height: 0;
	position: absolute;
	left: 0;
	top: 0;
}
.cssload-dot:before {
	content: "";
	width: 34px;
	height: 34px;
	border-radius: 49px;
	background: rgb(251, 211, 1);
	position: absolute;
	left: 50%;
	transform: translateY(0);
	-o-transform: translateY(0);
	-ms-transform: translateY(0);
	-webkit-transform: translateY(0);
	-moz-transform: translateY(0);
	margin-left: -17.5px;
	margin-top: -17.5px;
}

.cssload-dot:nth-child(5):before {
	z-index: 100;
	width: 44.5px;
	height: 44.5px;
	margin-left: -21.75px;
	margin-top: -21.75px;
	animation: cssload-dot-colors 4.6s ease infinite;
	-o-animation: cssload-dot-colors 4.6s ease infinite;
	-ms-animation: cssload-dot-colors 4.6s ease infinite;
	-webkit-animation: cssload-dot-colors 4.6s ease infinite;
	-moz-animation: cssload-dot-colors 4.6s ease infinite;
}

.cssload-dot:nth-child(1) {
	animation: cssload-dot-rotate-1 4.6s 0s linear infinite;
	-o-animation: cssload-dot-rotate-1 4.6s 0s linear infinite;
	-ms-animation: cssload-dot-rotate-1 4.6s 0s linear infinite;
	-webkit-animation: cssload-dot-rotate-1 4.6s 0s linear infinite;
	-moz-animation: cssload-dot-rotate-1 4.6s 0s linear infinite;
}
.cssload-dot:nth-child(1):before {
	background-color: rgb(255, 50, 112);
	animation: cssload-dot-move 4.6s 0s ease infinite;
	-o-animation: cssload-dot-move 4.6s 0s ease infinite;
	-ms-animation: cssload-dot-move 4.6s 0s ease infinite;
	-webkit-animation: cssload-dot-move 4.6s 0s ease infinite;
	-moz-animation: cssload-dot-move 4.6s 0s ease infinite;
}

.cssload-dot:nth-child(2) {
	animation: cssload-dot-rotate-2 4.6s 1.15s linear infinite;
	-o-animation: cssload-dot-rotate-2 4.6s 1.15s linear infinite;
	-ms-animation: cssload-dot-rotate-2 4.6s 1.15s linear infinite;
	-webkit-animation: cssload-dot-rotate-2 4.6s 1.15s linear infinite;
	-moz-animation: cssload-dot-rotate-2 4.6s 1.15s linear infinite;
}
.cssload-dot:nth-child(2):before {
	background-color: rgb(32, 139, 241);
	animation: cssload-dot-move 4.6s 1.15s ease infinite;
	-o-animation: cssload-dot-move 4.6s 1.15s ease infinite;
	-ms-animation: cssload-dot-move 4.6s 1.15s ease infinite;
	-webkit-animation: cssload-dot-move 4.6s 1.15s ease infinite;
	-moz-animation: cssload-dot-move 4.6s 1.15s ease infinite;
}

.cssload-dot:nth-child(3) {
	animation: cssload-dot-rotate-3 4.6s 2.3s linear infinite;
	-o-animation: cssload-dot-rotate-3 4.6s 2.3s linear infinite;
	-ms-animation: cssload-dot-rotate-3 4.6s 2.3s linear infinite;
	-webkit-animation: cssload-dot-rotate-3 4.6s 2.3s linear infinite;
	-moz-animation: cssload-dot-rotate-3 4.6s 2.3s linear infinite;
}
.cssload-dot:nth-child(3):before {
	background-color: rgb(175, 225, 2);
	animation: cssload-dot-move 4.6s 2.3s ease infinite;
	-o-animation: cssload-dot-move 4.6s 2.3s ease infinite;
	-ms-animation: cssload-dot-move 4.6s 2.3s ease infinite;
	-webkit-animation: cssload-dot-move 4.6s 2.3s ease infinite;
	-moz-animation: cssload-dot-move 4.6s 2.3s ease infinite;
}

.cssload-dot:nth-child(4) {
	animation: cssload-dot-rotate-4 4.6s 3.45s linear infinite;
	-o-animation: cssload-dot-rotate-4 4.6s 3.45s linear infinite;
	-ms-animation: cssload-dot-rotate-4 4.6s 3.45s linear infinite;
	-webkit-animation: cssload-dot-rotate-4 4.6s 3.45s linear infinite;
	-moz-animation: cssload-dot-rotate-4 4.6s 3.45s linear infinite;
}
.cssload-dot:nth-child(4):before {
	background-color: rgb(251, 211, 1);
	animation: cssload-dot-move 4.6s 3.45s ease infinite;
	-o-animation: cssload-dot-move 4.6s 3.45s ease infinite;
	-ms-animation: cssload-dot-move 4.6s 3.45s ease infinite;
	-webkit-animation: cssload-dot-move 4.6s 3.45s ease infinite;
	-moz-animation: cssload-dot-move 4.6s 3.45s ease infinite;
}

@keyframes cssload-dot-move {
	0% {
		transform: translateY(0);
	}
	18%,
	22% {
		transform: translateY(-68px);
	}
	40%,
	100% {
		transform: translateY(0);
	}
}

