.prices {
	background-color: #f3f3f3;
	padding: 60px 0;
	&__container {
	}

	&__info {
		max-width: 460px;
		text-align: center;
		margin: 0 auto;
		margin-bottom: 60px;
	}

	&__title {
		margin-bottom: 40px;
	}

	&__cards {
		display: flex;
		justify-content: space-between;
		margin: 0 auto;
		max-width: 1000px;
	}

	&__card {
		flex: 0 0 28%;
	}
	@media (max-width: $md3+px) {
		&__info {
			margin-bottom: 30px;
		}

		&__title {
			margin-bottom: 20px;
		}
		&__cards {
			flex-wrap: wrap;
			justify-content: center;
		}
		&__card {
			flex: 0 0 40%;
			&:first-child {
				margin-right: 40px;
			}
			&:not(:last-child) {
				margin-bottom: 40px;
			}
		}
	}
	@media (max-width: $md4+px) {
		&__card {
			flex: 0 0 100%;
			&:first-child {
				margin-right: 0;
			}
		}
	}
}

.p-card {
	position: relative;
	background-color: #fff;

	&:hover {
		.p-card__external {
			opacity: 0;
			visibility: hidden;
		}
		.p-card__interior {
			opacity: 1;
			visibility: visible;
		}
	}

	&__external {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		transition: opacity 0.4s ease-in;
	}

	&__status {
		width: 100%;
		padding-top: 25px;
		padding-bottom: 25px;
		background-color: $accent-color;
		color: #ffffff;

		font-family: $ffOS;
		font-weight: 700;
		font-size: 20px;
		text-align: center;
		text-transform: uppercase;
	}

	&__price {
		max-width: 100px;
		margin-bottom: 10px;
		padding-top: 100px;
		padding-bottom: 100px;

		font-family: $ffOS;
		font-weight: 700;
		font-size: 35px;
		text-align: center;
	}
	&__price--hidden {
		display: none;

		flex: 0 0 auto;
		max-width: none;
		margin-bottom: 10px;
		padding-top: 0px;
		padding-bottom: 0px;
		padding-left: 35px;

		text-align: left;
	}

	&__period {
		color: #81868e;

		font-weight: 400;
		font-family: $ffOS;
		font-size: 20px;

		transition: opacity 0.4s ease-in;
	}

	&__interior {
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		opacity: 0;
		visibility: hidden;

		transition: opacity 0.4s ease-in;
	}

	&__list {
		flex: 1 0 auto;
		padding: 35px;
	}

	&__item {
		color: #81868e;

		font-family: $ffOS;
		font-size: 14px;

		transition: opacity 0.4s ease-in;
		&:not(:last-child) {
			margin-bottom: 10px;
		}
	}

	&__link {
		flex: 0 0 auto;
		display: block;
		background-color: $accent-color;
		padding: 27px 0;
		color: #ffffff;

		font-family: $ffOS;
		font-size: 16px;
		text-align: center;

		transition: opacity 0.4s ease-in;
	}
	@media (max-width: $md3+px) {
		&__external {
			opacity: 0;
			visibility: hidden;
			display: none;
		}
		&__interior {
			position: relative;
			opacity: 1;
			visibility: visible;
		}
		&__list {
			padding: 15px;
		}
		&__price--hidden {
			display: block;
			padding-left: 15px;
		}
	}
	@media (max-height: $md4+px) and (orientation: landscape) {
		&__external {
			opacity: 0;
			visibility: hidden;
			display: none;
		}
		&__interior {
			position: relative;
			opacity: 1;
			visibility: visible;
		}
		&__price--hidden {
			display: block;
		}
	}
}
