.team {
	padding-top: 60px;
	&__container {
		max-width: 1060px;
		padding-bottom: 60px;
	}

	&__promo {
		max-width: 460px;
		margin: 0 auto;
		margin-bottom: 60px;
		text-align: center;
	}

	&__title {
		margin-bottom: 40px;
	}
	@media (max-width: $md3+px) {
		&__title {
			margin-bottom: 20px;
		}
		&__promo {
			margin-bottom: 30px;
		}
	}
}

.team-swiper {
	&__container {
		max-width: 1030px;
		padding-bottom: 60px;
		@media (max-width: $md3+px) {
			padding-bottom: 20px;
		}
	}
}

.team-swiper__container .swiper-pagination-bullets {
	bottom: 0;
}
.team-swiper__container .swiper-pagination-bullet {
	width: 10px;
	height: 10px;
	background-color: transparent;
	border: 1px solid $accent-color;
}
.team-swiper__container .swiper-pagination-bullet-active {
	background-color: $accent-color;
	border: 1px solid $accent-color;
}

.team-slide {
	&__wrapper {
		display: flex;
		justify-content: space-between;
	}

	&__image {
		padding-left: 50px;
		padding-right: 50px;
	}

	&__img {
		width: 300px;
		height: 400px;
		object-fit: cover;
	}

	&__info {
		max-width: 410px;
		padding-right: 40px;

		text-align: right;
	}
	@media (max-width: $md3+px) {
		&__wrapper {
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}
		&__image {
			margin-bottom: 20px;
			align-self: center;
		}
		&__img {
			height: 400px;
		}
		&__info {
			padding-left: 40px;
			max-width: none;

			text-align: center;
		}
	}
	@media (max-width: $md4+px) {
		&__img {
			height: 350px;
		}
		&__info {
			padding-right: 10px;
			padding-left: 10px;
		}
	}
}

.ts-info {
	&__wrapper {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
	}
	&__about {
		position: relative;
		&::after {
			content: "";
			position: absolute;
			top: 0;
			right: -40px;

			width: 5px;
			height: 100%;
			background-color: $accent-color;
		}
	}

	&__name {
		color: #464646;
		margin-bottom: 10px;

		font-family: $ffOS;
		font-size: 20px;
	}

	&__profession {
		color: #81868e;

		font-family: $ffSS;
		font-size: 14px;
		font-weight: 300;
		text-transform: uppercase;
	}

	&__text {
		margin-top: 30px;
		margin-bottom: 50px;
	}

	&__progress-bar {
		width: 100%;
	}
	@media (max-width: $md3+px) {
		&__text {
			margin-top: 10px;
			margin-bottom: 20px;
		}
		&__wrapper {
			align-items: stretch;
		}
	}
	@media (max-width: $md4+px) {
		&__about {
			&::after {
				right: -10px;
			}
		}
	}
}

.progress-bar {
	display: flex;
	flex-direction: column;
	margin-right: 0px;

	&__branding {
		position: relative;
		overflow: hidden;

		margin-bottom: 20px;
	}

	&__info {
		display: flex;
		justify-content: space-between;
		margin-bottom: 8px;
	}

	&__title {
		color: #81868e;

		font-family: $ffSS;
		font-weight: 300;
		font-size: 16px;
		text-transform: uppercase;
	}

	&__value {
		font-family: $ffSS;
		font-weight: 300;
		font-size: 16px;
		text-transform: uppercase;
	}

	&__progress {
		position: absolute;
		left: -777px;
		overflow: hidden;

		width: 0;
		height: 0;
	}

	&__bg {
		position: relative;
		overflow: hidden;

		background: #e6e9ed;
		height: 5px;
	}

	&__bar {
		position: absolute;
		overflow: hidden;
		top: 0;
		left: 0;

		background: $accent-color;
		height: 100%;
	}
	&__bar--65 {
		width: 65%;
	}
	&__bar--75 {
		width: 75%;
	}
	&__bar--80 {
		width: 80%;
	}
	&__web-design {
		margin-bottom: 20px;
	}
}

.t-footer {
	position: relative;
	z-index: 1;

	padding: 60px 0;
	background: url(../img/team/baner-bg.jpeg) center/cover no-repeat;
	filter: grayscale(100%);
	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;

		width: 100%;
		height: 100%;

		background-color: rgba(0, 0, 0, 0.8);
	}
	&__container {
		position: relative;
		z-index: 2;

		text-align: center;
	}

	&__text {
		margin-bottom: 20px;
		color: #ffffff;

		font-family: $ffOS;
		font-size: 20px;
	}

	&__link {
		border: 1px solid #fff;
		color: #ffffff;
		&:hover {
			background-color: #fff;
			border: 1px solid black;
			color: black;
		}
	}
	@media (max-width: $md3+px) {
		padding: 30px 0;
		&__text {
			margin-bottom: 10px;

			font-size: 14px;
		}
	}
}
